.project {
   &__wrapper {
      position: relative;
   }

   &__items {
      background-color: #121316;
   }

   &__item {
      max-width: 968px;
   }

   &__item-img {
      position: relative;
   }

   &__img-laptop {
      width: calc(892px * 100% / 968px);
      padding-top: calc(514px * 100% / 968px);
      max-width: 892px;
      max-height: 514px;

      position: relative;
      img {
         position: absolute;
         top: 0;
         left: 0;

         z-index: 2;
      }
      img:nth-child(2) {
         top: 28px;
         left: 50%;
         transform: translateX(-50%);
         z-index: 1;
      }
   }

   &__img-phone {
      position: absolute;
      bottom: calc(-25px * 100% / 514px);
      right: 0;
      z-index: 2;

      width: calc(202px * 100% / 968px);
      max-width: 202px;
      max-height: 411px;
   }

   &__item-txt {
      line-height: 150%;
      text-align: center;
      mark {
         background: $orange;
      }
   }

   &__button {
      display: block;
   }

   &__img-phone,
   &__img-laptop {
      overflow: hidden;
      img {
         width: 100%;
         height: 100%;
         object-fit: contain;
      }
   }

   &__wrap,
   &__button {
      margin: 0 auto;
   }
}

.slider {
   position: relative;
}

.swiper-button-next,
.swiper-button-prev {
   height: 100%;
   width: 70px;
   margin: 0;
   top: 0;
}

.swiper-button-next {
   right: 0%;
}

.swiper-button-prev {
   left: 0%;
}

.swiper-pagination.swiper-pagination-horizontal {
   display: flex;
   justify-content: center;
   align-items: center;

   height: 100%;
   max-height: 25px;
   top: 52vmin;
   left: 50%;
   transform: translateX(-50%);
}

.swiper-pagination-bullet {
   background-color: $orange;
}

@media screen and (min-width: 320px) {
   .project {
      &__wrap {
         padding: 35px 0;
      }
      &__items {
         max-width: 755px;
      }
      &__item-txt {
         font-size: 16px;
         margin-bottom: 20px;
      }
      &__item-img {
         margin-bottom: 29px;
      }
   }
}
@media screen and (min-width: 481px) {
   .project {
      &__wrap {
         padding: 40px 0;
      }
      &__item-img {
         margin-bottom: 43px;
      }
      &__item-txt {
         margin-bottom: 28px;
      }
   }
}
@media screen and (min-width: 769px) {
   .project {
      &__wrap {
         padding: 50px 0 45px;
      }
      &__item-img {
         margin-bottom: 62px;
      }
      &__item-txt {
         font-size: 20px;
         margin-bottom: 36px;
      }
   }
}
@media screen and (min-width: 1024px) {
   .project {
      &__wrap {
         padding: 60px 0 40px;
      }
      &__items {
         max-width: 800px;
      }
      &__item-img {
         margin-bottom: 68px;
      }
   }
}
@media screen and (min-width: 1171px) {
   .project {
      &__item-img {
         margin-bottom: 69px;
      }
   }

   .swiper-button-next {
      right: 4%;
   }

   .swiper-button-prev {
      left: 4%;
   }
}
