.footer{
   &__wrap{
      border-top: 1px solid #fff;

      padding: 60px 0 50px 0;

      text-align: center;
   }
   &__title{
      margin-bottom: 36px !important;
   }
   &__social{
      justify-content: center;
   }
   &__rights{
      &_mg-b-10{
         margin-bottom: 10px;
      }
   }
   &__button{
      margin-bottom: 72px;
   }
}
.mail{
   margin: 0 auto;
}

@media screen and (min-width: 320px){
   .footer{
      &__wrap{
         margin-top: 35px;
      }
      &__social{
         margin-bottom: 26px;
      }
      &__rights{
         font-size: 13px;
         line-height: 17px;
         text-align: center;
      }
   }
   .mail{
      font-size: 16px;
      line-height: 21px;

      margin-bottom: 26px;
   }
}
@media screen and (min-width: 481px){
   .footer{
      &__wrap{
         margin-top: 40px;
      }
      &__social{
         margin-bottom: 30px;
      }
   }
   .mail{
      font-size: 20px;
      line-height: 26px;

      margin-bottom: 30px;
   }
}
@media screen and (min-width: 769px){
   .footer{
      &__wrap{
         margin-top: 45px;
      }
      &__rights{
         font-size: 15px;
         line-height: 20px;
      }
   }
   .mail{
      font-size: 24px;
      line-height: 31px;
   }
}
@media screen and (min-width: 1000px){
   .footer{
      &__wrap{
         margin-top: 40px;
      }
      &__rights{
         text-align: left;
      }
   }
}
