.overlay {
   position: fixed;
   bottom: 0;
   left: 0;
   z-index: 5;

   width: 100%;
   background-color: rgb(18, 19, 22, 0.8);

   display: none;
   opacity: 0;

   &.active {
      display: block;
   }

   &__image {
      position: absolute;
      top: 50%;
      left: 50%;
      max-height: 82%;
      overflow: hidden;
      opacity: 0;
   }

   &__close-btn {
      position: absolute;
      top: 20px;
      right: 20px;
      cursor: pointer;

      > svg {
         fill: white;
      }
   }
}

@media screen and (min-width: 320px) {
   .overlay {
      height: calc(100% - 75px);

      &__image {
         max-width: 90%;
      }

      &__close-btn {
         top: 10px;
         right: 10px;
         width: 32px;
         height: 32px;
      }
   }
}

@media screen and (min-width: 768px) {
   .overlay {
      &__image {
         max-width: 70%;
      }

      &__close-btn {
         top: 20px;
         right: 20px;
      }
   }
}
