[class*="title"]{
   text-align: center;
   font-weight: 400;
}

[class*="title_blue"]{
   color: $blue
}

[class*="title_orange"]{
   color: $orange;
}

@media screen and (min-width: 320px){
   [class*="title"]{
      font-size: 24px;
      line-height: 31px;
      margin-bottom: 30px;
   }
}

@media screen and (min-width: 481px){
   [class*="title"]{
      font-size: 32px;
      line-height: 42px;
      margin-bottom: 40px;
   }
}

@media screen and (min-width: 769px){
   [class*="title"]{
      font-size: 48px;
      line-height: 62px;
      margin-bottom: 50px;
   }
}
