.cert {
   &__wrap {
      max-width: 971px;
      width: 100%;
      margin: 0 auto;
   }

   &__items {
      display: flex;
      column-gap: calc(34px * 100% / 971px);
      position: relative;
   }

   &__item {
      max-width: 301px;
   }

   &__item-img {
      width: 100%;
      height: 100%;
      max-height: 214px;
      min-height: 209px;
      overflow: hidden;
      background: rgba(44, 44, 44, 0.551);
      text-align: center;
      cursor: pointer;
      transition: transform 0.3s linear;

      img {
         height: auto;
         max-height: 214px;
         max-width: 100%;
      }

      @media (hover: hover) {
         &:hover {
            transform: scale(1.05);
         }
      }
   }

   &__item-txt {
      line-height: 150%;
      text-align: center;
   }
}

@media screen and (min-width: 320px) {
   .cert {
      &__wrap {
         padding: 35px 0;
      }
      &__items {
         flex-wrap: wrap;
         justify-content: center;
         row-gap: 30px;
      }
      &__item-img {
         margin-bottom: 20px;
      }
      &__item-txt {
         font-size: 16px;
      }
   }
}
@media screen and (min-width: 481px) {
   .cert {
      &__wrap {
         padding: 40px 0;
      }
      &__items {
         row-gap: 40px;
      }
      &__item-img {
         margin-bottom: 28px;
      }
      &__item-txt {
         font-size: 20px;
      }
   }
}
@media screen and (min-width: 769px) {
   .cert {
      &__wrap {
         padding: 50px 0;
      }
      &__items {
         row-gap: 52px;
      }
      &__item-img {
         margin-bottom: 36px;
      }
      &__item-txt {
         font-size: 24px;
      }
   }
}
@media screen and (min-width: 1000px) {
   .cert {
      &__wrap {
         padding: 60px 0;
      }
      &__items {
         flex-wrap: nowrap;
         row-gap: 0;
      }
   }
}
