.header {
   height: 100vh;

   &__container {
      height: 100%;
   }

   &__top-header {
      display: flex;
      align-items: center;
      position: sticky;
      top: 0;
      background: linear-gradient(
         180deg,
         rgba(18, 19, 22, 1) 45%,
         rgba(18, 19, 22, 0.9391106784510679) 80%,
         rgba(18, 19, 22, 0.8214636196275386) 100%
      );
      box-shadow: 0 -8px 18px 24px rgb(18 19 22 / 85%);
   }
   &__title-wrap {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: center;
   }
   &__title {
      line-height: calc(73px / 56px);
      margin: 0;
   }
   &__third-title {
      line-height: calc(42px / 32px);
   }
}
.nav {
   flex: 1 1 auto;
   &__list {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      max-width: 442px;
   }
   &__list-item {
      font-size: 20px;
      line-height: 26px;
      overflow: hidden;
      transition: all 0.4s ease-out;
      a {
         position: relative;
         transition: all 0.4s ease-out;
         padding: 8px;
      }
   }
}
.social {
   display: flex;
   align-items: center;
   &__linkedin-img,
   &__telegram-img {
      width: 21px;
      height: 21px;
      img {
         width: 100%;
         height: 100%;
      }
   }
}
@media screen and (min-width: 320px) and (max-width: 769px) {
   .burger {
      flex: 1 1 auto;
      &__items {
         display: flex;
         flex-direction: column;
         justify-content: center;

         height: 30px;
         max-width: 34px;
         min-width: 34px;

         transition: all 0.3s ease-in;
         span {
            display: inline-block;
            width: 100%;
            height: 2px;
            background-color: $white;
            transition: all 0.2s ease-in 30ms;
            &:nth-child(2) {
               margin: 4px 0;
            }
         }
      }
      &__items_active {
         transform: rotateY(190deg);
         span {
            &:nth-child(1) {
               transform: rotate(-45deg) translate(-5px, 4px);
            }
            &:nth-child(2) {
               background-color: inherit;
            }
            &:nth-child(3) {
               transform: rotate(45deg) translate(-4px, -4px);
            }
         }
      }
   }
   .burg-menu,
   .header__top-header {
      position: fixed;
      left: 0;
      width: 100%;
   }
   .burg-menu,
   .burg-menu__list {
      height: 0%;
   }
   .burg-menu__list_active,
   .burg-menu_active {
      height: 100%;
   }
   .burg-menu {
      top: 0;
      z-index: 5;

      background: #121316;
      box-shadow: inset 0 -20px 70px #121316;

      transition: all 0.6s ease-in;

      overflow: hidden;
      &__list {
         display: flex;
         align-items: center;
         flex-direction: column;

         margin: 0 auto;
         padding: 0;

         transition: all 0.2s ease-out 400ms;
         box-shadow: inset 0 -20px 70px #121316;
         &_active {
            height: 55%;

            padding-top: 122px;
            padding-bottom: 40px;

            transition: all 0.6s ease-out;
         }
      }
      &__item {
         font-size: 20px;
         line-height: 26px;

         padding: 10px;
         &:not(:last-child) {
            margin-bottom: 12px;
         }
         &:last-child {
            margin-top: 43px;
            margin-bottom: 0;
         }
         &_blue {
            color: $blue;
         }
      }
   }

   .header {
      &__top-header {
         z-index: 6;
      }
   }
}

@media screen and (min-width: 320px) {
   .header {
      &__top-header {
         padding-top: 12px;
         column-gap: 12px;

         padding: 14px 10px;
      }
      &__title,
      &__sec-title {
         font-size: 28px;
         margin-bottom: 16px;
      }
      &__third-title {
         font-size: 18px;
      }
   }
   .nav {
      display: none;
   }
   .social {
      a {
         filter: invert(0) sepia(1) saturate(1) hue-rotate(180deg) brightness(1.2);
         transition: all 0.4s ease-out;
         padding: 9px;
         &:first-child {
            padding-right: 14px;
         }
         &:last-child {
            padding-left: 14px;
         }
      }
   }
   .button {
      flex: 1 1 auto;
   }
}

@media screen and (min-width: 481px) {
   .header {
      &__top-header {
         padding-top: 8px;
         column-gap: 32px;

         padding: 18px 20px;
      }
      &__title,
      &__sec-title {
         font-size: 38px;
      }
      &__third-title {
         font-size: 26px;
      }
   }
   .social {
      a {
         &:first-child {
            padding-right: 16px;
         }
         &:last-child {
            padding-left: 16px;
         }
      }
   }
   .button {
      flex: 1 0 165px;
   }
}

@media screen and (min-width: 769px) {
   .burger {
      display: none;
   }
   .burg-menu {
      display: none;
   }

   .header {
      &__top-header {
         padding: 28px 0 18px;
      }
      &__title,
      &__sec-title {
         font-size: 53px;
      }
      &__title {
         margin-bottom: 18px;
      }
      &__sec-title {
         margin-bottom: 25px;
      }
      &__third-title {
         font-size: 32px;
      }
   }
   .nav {
      display: block;
      a {
         &::before,
         &::after {
            content: '"';
            position: absolute;
            top: 11px;

            font-size: 5px;
            line-height: 1;
            color: $orange;
            transition: all 0.3s ease-in-out;
         }
         &::before {
            left: -14px;
         }
         &::after {
            right: -14px;
         }
         &:hover,
         &:focus {
            color: $orange;
            &::before,
            &::after {
               content: '"';
               font-size: 20px;
            }
            &::before {
               left: 2px;
            }
            &::after {
               right: 2px;
            }
         }
      }
   }
   .social {
      a {
         &:hover {
            filter: invert(0.4) sepia(1) saturate(20) hue-rotate(180deg) brightness(1.2);
            transform: scale(1.2);
         }
      }
   }
   .button {
      &:hover {
         background: $orange;
         color: #121316;
         cursor: pointer;
      }
   }
}

@media screen and (min-width: 1000px) {
   .header {
      &__top-header {
         padding: 36px 0 10px;
      }
      &__title,
      &__sec-title {
         font-size: 56px;
         line-height: 73px;
      }
   }
}

@media screen and (min-width: 1171px) {
   .header {
      &__top-header {
         padding-bottom: 12px;
      }
   }
}
