.about {
   &__content {
      max-width: 970px;
      margin: 0 auto;

      display: grid;

      gap: 34px;
   }
   &__img {
      > img {
         object-fit: cover;
         object-position: top;
         width: 100%;
         height: 100%;
      }
   }
   &__txt {
      font-size: 24px;
      line-height: 150%;
      &:first-child {
         margin-bottom: 16px;
      }
   }
   &__achivs-items {
      display: flex;
      justify-content: space-between;
      max-height: 90px;
      max-width: 650px;
      width: 100%;

      justify-self: center;
   }
   &__item {
      color: $blue;
      text-align: center;
   }
}

@media screen and (min-width: 320px) {
   .about {
      &__wrap {
         padding: 35px 0;
      }
      &__title {
         margin-bottom: 30px;
      }
      &__content {
         grid-template-columns: 1fr;
         grid-auto-rows: auto;
         gap: 20px;
      }
      &__img {
         max-height: 400px;
         transition: all 0.6s ease;
      }
      &__inner {
         text-align: center;
      }
      &__txt,
      &__item {
         font-size: 16px;
         line-height: 24px;
      }
      &__txt {
         &:first-child {
            margin-bottom: 12px;
         }
      }
      &__item {
         &:nth-child(2) {
            min-width: 122px;
         }
         span {
            font-size: 32px;
            line-height: 1.1;
         }
      }
   }
}

@media screen and (min-width: 481px) {
   .about {
      &__wrap {
         padding: 40px 0;
      }
      &__title {
         margin-bottom: 40px;
      }
      &__img {
         max-height: 509px;
      }
      &__txt,
      &__item {
         font-size: 20px;
         line-height: 30px;
      }
      &__item span {
         font-size: 40px;
         line-height: 1.2;
      }
   }
}

@media screen and (min-width: 769px) {
   .about {
      &__wrap {
         padding: 55px 0;
      }
      &__title {
         margin-bottom: 50px;
      }
      &__content {
         grid-template-columns: minmax(280px, 370px) minmax(415px, 1fr);
         grid-template-rows: 1fr;
         column-gap: 14px;
         row-gap: 30px;
      }
      &__inner {
         text-align: start;
      }
      &__txt {
         font-size: 24px;
         line-height: 36px;
         &:first-child {
            margin-bottom: 16px;
         }
      }
      &__achivs-items {
         grid-column-start: 1;
         grid-column-end: 3;
      }
      &__item {
         font-size: 20px;
         line-height: 30px;
         &:nth-child(2) {
            min-width: 153px;
         }
      }
   }
}

@media screen and (min-width: 1000px) {
   .about {
      &__wrap {
         padding: 60px 0;
      }
      &__content {
         gap: 30px;
      }
      &__txt {
         margin-bottom: 40px;
      }
   }
}

